* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'montseratt', sans-serif;
}

h1{
  font-size: 50px;
  font-weight: 700;
  text-align: center;
  color: #fff;
  text-shadow: 5px 10px rgba(0, 0, 0, 0.2);
}

.app {
  background: url(./Images/image1.jpg) no-repeat bottom;
  background-size: cover;
  transition: 0.4 ease-out;
}

.app.norm {
  background: url(./Images/image2.jpg) no-repeat bottom;
  background-size: cover;
  transition: 0.4 ease-out;
}

.app.warm {
  background: url(./Images/image-warm.jpg) no-repeat bottom;
  background-size: cover;
  transition: 0.4 ease-out;
}

main {
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), (0, 0, 0, 0.75));
  padding: 25px;
}

.search-box {
  width: 100%;
  margin: 100px 0 75px;
}

.search-box .search-bar {
  display: block;
  width: 100%;
  padding: 15px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgb(255, 255, 255, 0.4);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);

  color: rgb(16, 4, 91);
  font-size: 20px;

  transition: 0.4s ease;
}

.location-box .location {
  color: #fff;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
}

.location-box .date {
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  text-align: center;
  font-style: italic;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.weather-box {
  text-align: center;
}

.weather-box .temp {
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;

  padding: 15px 25px;

  color: #fff;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 5px 10px rgba(50, 50, 70, 0.5);
  text-align: center;
  box-shadow: 5px 10px rgba(0, 0, 0, 0.2);
}

.weather-box .weather {
  color: #fff;
  font-size: 50px;
  font-weight: 700;
  text-shadow: 5px 5px rgba(50, 50, 70, 0.5);
}